html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 90%;
}

.contenedor {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  padding: 0;
}

.actionImg {
  cursor: pointer;
  opacity: 0.5;
  width: 3rem;
  height: auto;
  margin-left: 1rem;
  float: right;
}
.actionImg:hover {
  opacity: 1;
}
@media (min-width: 768px) {
  .actionImg {
    width: 2rem;
    height: auto;
  }
}

.boton {
  background-color: #0075be;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 500;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  margin: auto;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #00568b;
  box-shadow: 0 1px 3px 0 rgba(60, 64, 67, 0.302), 0 4px 8px 3px rgba(60, 64, 67, 0.149);
  cursor: pointer;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 2rem;
  color: #0a3a67;
  font-weight: 700;
}

h4 {
  font-size: 1.5rem;
  color: #6b7280;
  font-weight: 600;
}

p {
  font-size: 1.3rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
  font-size: 2rem;
}

.formulario {
  margin-top: 5rem;
}
.formulario legend {
  font-size: 2rem;
  margin-bottom: 4rem;
  color: #0a3a67;
  font-weight: 600;
  display: inline-block;
  text-align: center;
}
.formulario .campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.formulario .campo label {
  margin-bottom: 1rem;
}
.formulario .campo label span {
  color: #6b7280;
  font-size: 1.2rem;
}
.formulario .campo input, .formulario .campo textarea {
  width: 100%;
  padding: 1rem;
  border: none;
  background-color: #e5effa;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .formulario .campo input, .formulario .campo textarea {
    flex: 1;
  }
}
.formulario .campo input:focus, .formulario .campo textarea:focus {
  background-color: white;
  outline: none;
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.2);
}
.formulario .campo textarea {
  resize: none;
  overflow: auto;
}
.formulario .campo select {
  width: auto;
  border-radius: 1rem;
  padding: 0.5rem;
  background-color: #e5effa;
  border: none;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  display: block;
  padding: 1rem 1rem 1rem 2rem;
  font-weight: 600;
  margin-bottom: 1rem;
  max-width: 80rem;
  animation: slideIn 0.5s ease-in-out, slideOut 0.5s ease-in-out 2.5s forwards;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

@keyframes slideIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
  }
}
.fc-event-task-icon {
  width: 4rem;
  height: 4rem;
  margin: auto;
  cursor: pointer;
}

.fc .fc-toolbar-title {
  font-size: 2rem;
  color: #6b7280;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

table, thead, th, td {
  border: solid 2px #FFFFFF;
  border-collapse: collapse;
  text-align: center;
}
table img, thead img, th img, td img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.taskComments .taskCommentsTitle {
  margin: 0.5rem 0 0 0;
}
.taskComments .commentContainer {
  margin-top: 0.5rem;
  background-color: #bbd5f1;
  border-radius: 1rem;
  padding: 1rem;
}
.taskComments .commentContainer p {
  margin: 0;
}
.taskComments .commentContainer .infoUserComment {
  display: flex;
}
.taskComments .commentContainer .infoUserComment .userImg {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: 1rem;
}
@media (min-width: 768px) {
  .taskComments .commentContainer .infoUserComment .userImg {
    width: 2rem;
    height: 2rem;
  }
}
.taskComments .commentContainer .arrowTextComment {
  margin-top: 0.5rem;
  margin-left: 2rem;
  width: 0;
  height: 0;
  border-left: 1rem solid transparent;
  border-right: 1rem solid transparent;
  border-bottom: 1rem solid #FFFFFF;
}
.taskComments .commentContainer .textComment {
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 1rem;
  margin-bottom: 0.5rem;
}
.taskComments .commentContainer .dateComment {
  font-size: 1.2rem;
  text-align: right;
  margin-left: auto;
}

.checkboxCollaborators .checkboxCollaborator {
  display: flex;
  flex: 1;
  align-items: center;
  margin-bottom: 1rem;
  background-color: #bbd5f1;
  border-radius: 1rem;
  padding: 0.5rem;
}
.checkboxCollaborators .checkboxCollaborator input {
  margin-right: 1rem;
}
.checkboxCollaborators .checkboxCollaborator .userLabel {
  display: flex;
  align-items: center;
}
.checkboxCollaborators .checkboxCollaborator .userLabel .userImg {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: 1rem;
}
.checkboxCollaborators .checkboxCollaborator .userLabel .userName {
  margin: 0;
}

#invitationForm legend {
  margin-top: 2rem;
}
#invitationForm label {
  display: block;
}
#invitationForm input {
  margin-top: 1rem;
  width: 100%;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: #bbd5f1;
}
#invitationForm #invitationBtn {
  margin-top: 2rem;
}
#invitationForm .lds-ellipsis {
  display: none;
  position: relative;
  width: 80px;
  height: 5.8rem;
  margin: 1rem auto auto auto;
}
#invitationForm .lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #0075be;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
#invitationForm .lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
#invitationForm .lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
#invitationForm .lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
#invitationForm .lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

#collaborationContainer {
  list-style: none;
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  grid-gap: 1rem;
}
#collaborationContainer .collaborationBin {
  display: grid;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
#collaborationContainer .collaborationBin .collaborationTitle {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
#collaborationContainer .collaborationBin span {
  font-weight: 600;
}
#collaborationContainer .collaborationBin p {
  margin: 0 0 0.5rem 0;
}
#collaborationContainer .collaborationBin .collaborationAction {
  margin-top: 2rem;
  align-self: end;
}

.checkTask {
  margin-right: 1rem;
  width: 2rem;
  height: 2rem;
}

footer .footer-container {
  background-color: #0a3a67;
