.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 500ms;
  height: 0;
  overflow: hidden;
}
.sidebar.show {
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(2px);
  z-index: 99;
}
@media (min-width: 768px) {
  .sidebar.show {
    display: none;
  }
}
.sidebar.hide {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: auto;
    background-color: #0a3a67;
    justify-content: space-between;
    padding: 2rem;
    margin: 0.7rem;
    border-radius: 1rem;
    margin-right: 0;
  }
}
.sidebar .mobileCloseBtn {
  cursor: pointer;
  padding: 2rem;
}
.sidebar .mobileCloseBtn img {
  float: right;
  width: 3rem;
  height: 3rem;
}
@media (min-width: 768px) {
  .sidebar .mobileCloseBtn {
    display: none;
  }
}
.sidebar .sidebar-header a {
  width: 100%;
  display: flex;
  justify-content: center;
}
.sidebar .sidebar-nav {
  padding: 3rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
