@use 'base/variables' as v;
@use 'base/mixins' as m;

.checkboxCollaborators {
    .checkboxCollaborator {
        display: flex;
        flex: 1;
        align-items: center;
        margin-bottom: 1rem;
        background-color: v.$azulLiso;
        border-radius: 1rem;
        padding: .5rem;

        input {
            margin-right: 1rem;
        }

        .userLabel {
            display: flex;
            align-items: center;

            .userImg {
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                margin-right: 1rem;

            }

            .userName {
                margin: 0;
            }
        }

    }
}

#invitationForm {
    legend {
        margin-top: 2rem;
    }

    label {
        display: block;
    }

    input {
        margin-top: 1rem;
        width: 100%;
        padding: 1rem;
        border: none;
        border-radius: 1rem;
        background-color: v.$azulLiso;
    }

    #invitationBtn {
        margin-top: 2rem;
    }

    // #loading-overlay-invitation {
    //     display: none;
    //     width: 4.8rem;
    //     height: 4.8rem;
    //     margin: 2rem auto auto auto;
    .lds-ellipsis {
        display: none;
        position: relative;
        width: 80px;
        height: 5.8rem;
        margin: 1rem auto auto auto;
    }
    
    .lds-ellipsis div {
        position: absolute;
        top: 33px;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background: v.$azul;
        animation-timing-function: cubic-bezier(0, 1, 1, 0);
    }
    
    .lds-ellipsis div:nth-child(1) {
        left: 8px;
        animation: lds-ellipsis1 0.6s infinite;
    }
    
    .lds-ellipsis div:nth-child(2) {
        left: 8px;
        animation: lds-ellipsis2 0.6s infinite;
    }
    
    .lds-ellipsis div:nth-child(3) {
        left: 32px;
        animation: lds-ellipsis2 0.6s infinite;
    }
    
    .lds-ellipsis div:nth-child(4) {
        left: 56px;
        animation: lds-ellipsis3 0.6s infinite;
    }
    
    @keyframes lds-ellipsis1 {
        0% {
            transform: scale(0);
        }
    
        100% {
            transform: scale(1);
        }
    }
    
    @keyframes lds-ellipsis3 {
        0% {
            transform: scale(1);
        }
    
        100% {
            transform: scale(0);
        }
    }
    
    @keyframes lds-ellipsis2 {
        0% {
            transform: translate(0, 0);
        }
    
        100% {
            transform: translate(24px, 0);
        }
    }
}

